import React, { useState } from "react";
import { Link } from "react-router-dom";
import { links } from "./Mylinks";

const NavLinks = ({ handleLinkClick }) => {
  const [heading, setHeading] = useState("");
  const [subHeading, setSubHeading] = useState("");

  const handleHeadingClick = (linkName) => {
    if (heading === linkName) {
      setHeading("");
      setSubHeading("");
    } else {
      setHeading(linkName);
      setSubHeading("");
    }
  };

  const handleSubHeadingClick = (subHead) => {
    if (subHeading === subHead) {
      setSubHeading("");
    } else {
      setSubHeading(subHead);
    }
  };

  return (
    <>
      {links.map((link, index) => (
        <div key={index}>
          <div className="px-3 text-left md:cursor-pointer group">
            <h1
              className="py-7 flex justify-between items-center md:pr-0 pr-5 group"
              onClick={() => handleHeadingClick(link.name)}
            >
              {link.name}
              <span className="text-xl md:hidden inline">
                <ion-icon
                  name={`${
                    heading === link.name ? "chevron-up" : "chevron-down"
                  }`}
                ></ion-icon>
              </span>
            </h1>
            {link.submenu && (
              <div>
                <div className="absolute top-full hidden group-hover:block hover:block">
                  <div className="py-3">
                    <div className="w-4 h-4 left-3 absolute mt-1 bg-white rotate-45"></div>
                  </div>
                  <div className="bg-white p-5 grid grid-cols-1 md:grid-cols-3 gap-10">
                    {link.sublinks.map((mysublinks, subIndex) => (
                      <div key={subIndex}>
                        <h1 className="text-lg font-semibold">
                          {mysublinks.Head}
                        </h1>
                        {mysublinks.sublink.map((slink, slinkIndex) => (
                          <li
                            key={slinkIndex}
                            className="text-sm text-gray-600 my-2.5"
                          >
                            <Link
                              to={slink.link}
                              className="hover:text-primary"
                              onClick={handleLinkClick}
                            >
                              {slink.name}
                            </Link>
                          </li>
                        ))}
                      </div>
                    ))}
                  </div>
                </div>
              </div>
            )}
          </div>

          {/* Mobile menus */}
          <div
            className={`${
              heading === link.name ? "block" : "hidden"
            } md:hidden`}
          >
            {link.sublinks.map((slinks, slinksIndex) => (
              <div key={slinksIndex}>
                <h1
                  onClick={() => handleSubHeadingClick(slinks.Head)}
                  className="py-4 pl-7 font-semibold flex justify-between items-center"
                >
                  {slinks.Head}
                  <span className="text-xl">
                    <ion-icon
                      name={`${
                        subHeading === slinks.Head
                          ? "chevron-up"
                          : "chevron-down"
                      }`}
                    ></ion-icon>
                  </span>
                </h1>
                <div
                  className={`${
                    subHeading === slinks.Head ? "block" : "hidden"
                  }`}
                >
                  {slinks.sublink.map((slink, slinkIndex) => (
                    <li key={slinkIndex} className="py-3 pl-14">
                      <Link
                        to={slink.link}
                        className="hover:text-primary"
                        onClick={handleLinkClick}
                      >
                        {slink.name}
                      </Link>
                    </li>
                  ))}
                </div>
              </div>
            ))}
          </div>
        </div>
      ))}
    </>
  );
};

export default NavLinks;
